@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,700;1,400&display=swap);
.icon{
    width: 100px;
    height: 100px;
    background-color: #ffffff;
    padding: 15px;
    border-radius: 50%;
}

.App{
    min-height: 100%;
    color: #ffffff;
    background-color: #1975d2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.modal::before{
    display: block;
}

.modal{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    background-color: #00000080;
    z-index: 3;
}

.modal-content{
    background-color: #1975d2;
    width: 90%;
    margin: 15px auto;
    position: relative;
    padding: 15px;
}

.modal-content header{
    background-color: #1975d2;
    display: flex;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.modal-content .title-group{
    flex-grow: 1;
    text-align: center;
}

.modal-content .title-group h2{
    font-size: 2.441em;
    margin-bottom: 15px;
}

.modal-content .close-btn{
    font-size: 1.25em;
    background: none;
    border: none;
    color: inherit;
    font-weight: 700;
    cursor: pointer;
}

h1{
    font-size: 3.052em;
    text-align: center;
    margin: 0 15px 15px 15px;
}

.quote{
    margin: 15px;
    font-size: 1.25em;
}

.quote q{
    font-weight: 400;
    font-size: 1.25em;
    font-style: italic;
}

.quote p{
    text-align: right;
    margin-top: 5px;
}

footer{
    width: 100%;
    padding: 15px;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

footer p{
    text-align: center;
    margin: 0 5px;
}

footer .bullet{
    background-color: yellowgreen;
    flex: 1 1;
}

footer a{
    font-weight: 700;
    text-decoration: none;
    color: inherit
}

footer button{
    background: none;
    border: none;
    font-size: inherit;
    font-weight: 700;
    color: inherit;
    cursor: pointer;
}

ul{
    list-style-type: none;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

li{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;
    margin: 15px;
    border: 1px solid #ffffff31;
    cursor: pointer;
    transition: background 0.5s;
}

li:hover{
    background-color: #ffffff31;
}

li p, li q{
    text-align: center;
    margin-top: 15px;
}
*{
    margin: 0;
    padding: 0;
    outline: none;
    box-sizing: border-box;
}

html, body, #root{
    height: 100%;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 300;
}
